import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { WpapiService } from 'src/app/services/wpapi.service';
import { AuthService } from '../services/auth.service';
import { ToastController } from '@ionic/angular';

@Component({
  selector: 'app-signup',
  templateUrl: './signup.page.html',
  styleUrls: ['./signup.page.scss']
})
export class SignupPage implements OnInit {
  name;
  email;
  password;

  acceptTerms = false;
  acceptPrivacy = false;

  showPass = false;

  constructor(
    private router: Router,
    private wpApiService: WpapiService,
    private authService: AuthService,
    public toastController: ToastController
  ) {}

  goTo(page) {
    this.router.navigateByUrl(page);
  }

  async togglePass() {
    if (this.showPass) {
      this.showPass = false;
    } else {
      this.showPass = true;
    }
  }

  async presentToast(message, color, position, duration) {
    const toast = await this.toastController.create({
      message: message,
      color: color,
      position: position,
      duration: duration
    });
    toast.present();
  }

  

  async ngOnInit() {
    // await this.facebookLogin();
  }

  register(form) {
    this.wpApiService.register(form.value).subscribe(data => {
      console.log(data);
      if (data['status'] === 0) {
        this.presentToast(
          data['msg'] + ', Please try again',
          'danger',
          'top',
          3000
        );
      } else {
        this.presentToast(
          'Your Account have been created successfully, we will take you to login.',
          'success',
          'top',
          3000
        );
        setTimeout(() => {
          this.router.navigateByUrl('/login');
        }, 3200);
      }
    });
  }

  // loginFacebook() {
  //   // this.fb.getLoginStatus().then(data => {
  //   //   console.log(data);
  //   // });
  //   this.fb.login(['public_profile', 'user_friends', 'email'])
  // .then((res: FacebookLoginResponse) => console.log('Logged into Facebook!', res))
  // .catch(e => console.log('Error logging into Facebook', e));

  // // this.fb.logEvent(this.fb.EVENTS.EVENT_NAME_ADDED_TO_CART);
  // }
}
